* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-color: #0e0e2e;
  overscroll-behavior: none;
  overflow-x: hidden;
}

body::-webkit-scrollbar {
  width: 6px;
}
body::-webkit-scrollbar-track {
  border-radius: 10px;
  background-color: rgba(37, 37, 128, 0.404);
}
body::-webkit-scrollbar-thumb {
  background-image: linear-gradient(45deg, #e4ba9b, #a68eff);
  border-radius: 10px;
}

a {
  cursor: pointer;
}
.wrap {
  /* position: fixed; */
  width: 100%;
  height: 100%;
  /* overflow: auto; */
  /* scroll-behavior: smooth; */
}
html {
  /* scroll-behavior: smooth; */
}

.moonBackground {
  position: fixed;
  right: 17%;
  top: 15%;
  width: 5%;
}

.hidden {
  display: none !important;
}

.background {
  z-index: 1;
  width: 100vw;
  background-color: #0e0e2e;
  min-height: 100vh;
  background-image: url('../assets/images/starryStar2.png');
  background-repeat: repeat;
  background-size: contain;
  height: 20000px;
  display: flex;
  flex-direction: column;
}
.backgroundTop {
  z-index: 1;
  width: 100vw;
  background-color: #0e0e2e;
  object-fit: none;
}

.center {
  display: flex;
  justify-content: center;
  align-items: center;
}

.flex {
  display: flex;
}

.alignCenter {
  display: flex;
  align-items: center;
}

.justifyCenter {
  display: flex;
  justify-content: center;
}

.directionColumn {
  display: flex;
  flex-direction: column;
}

.imgObjectFitContain {
  object-fit: contain;
}

.positionAbsolute {
  position: absolute;
}

.positionRelative {
  position: relative;
}

.firstWorldOnlyOne {
  margin: 17.7px 0 42.6px;
  font-family: MaruBuriot;
  font-size: 34.9px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.43;
  letter-spacing: normal;
  text-align: center;
  color: #e4ba9b;
}

.flag {
  position: absolute;
  bottom: -27px;
}

.atLastFont {
  margin: 6.8px 0 34.8px;
  font-family: MaruBuriot;
  font-size: 34.9px;
  font-weight: 200;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.43;
  letter-spacing: normal;
  text-align: center;
  color: #e4ba9b;
}

.fingerFrame {
  border: 40px solid;
  border-image-source: url('../assets/images/fingerFrame2.png');
  width: 788px;
  margin: 100px 0;
  border-image-slice: 40;
  display: flex;
  flex-direction: column;
}

.lastStoryOpen {
  font-family: MaruBuriot;
  font-size: 29.9px;
  font-weight: 300;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.67;
  letter-spacing: normal;
  text-align: center;
  color: #e4ba9b;
  height: 50px;
  margin-top: -20px;
}

.fingerToggleContainer {
  bottom: -188px;
  width: 100%;
}

.fingerToggle {
  background: url('../assets/images/fingerDown.png') no-repeat;
  width: 87px;
  height: 179px;
}
.open .fingerToggle {
  background: url('../assets/images/fingerUp.png') no-repeat;
  width: 87px;
  height: 179px;
}
.fingerToggle:hover {
  animation: fingerUpEffect 0.1s linear forwards;
}
.open .fingerToggle:hover {
  animation: fingerDownEffect 0.1s linear forwards;
}

@keyframes fingerUpEffect {
  from {
    transform: translateY(0px);
  }

  to {
    transform: translateY(10px);
  }
}
@keyframes fingerDownEffect {
  from {
    transform: translateY(0px);
  }

  to {
    transform: translateY(-10px);
  }
}
.storyListContainer {
  max-height: 0;
  overflow: hidden;
  transition: all 0.5s ease-in-out;
}
.open .storyListContainer {
  max-height: 800px;
  overflow: auto;
}

.storyItemContainer {
  position: relative;
  display: flex;
  padding: 25px 0;
  margin: 0 10px;
  justify-content: space-between;
}
.storyItemContainer:not(:last-child) {
  border-bottom: 1px solid rgba(228, 186, 155, 0.5);
}
.storyItemContainer > .flex > img {
  margin-right: 25px;
}
.storyItemContainer .arrowRight:hover {
  animation: buttonEffect 0.1s linear forwards;
  transform-origin: left;
}
@keyframes buttonEffect {
  from {
    transform: scale(1);
  }

  to {
    transform: scale(1.2);
  }
}
.storyTitleFont {
  font-family: MaruBuriot;
  font-size: 23.9px;
  font-weight: 300;
  font-stretch: normal;
  font-style: normal;
  line-height: 2.09;
  letter-spacing: normal;
  color: #fff;
}
.storyContentFont {
  font-family: MaruBuriot;
  font-size: 18px;
  font-weight: 200;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.5;
  letter-spacing: normal;
  color: #fff;
}

.fadeContainer {
  z-index: 10;
}

.fadeContainer .hiarline {
  width: 1px;
  height: 76px;
  background-color: rgba(228, 186, 155, 0.5);
  margin: 17px;
}

.fadeContainer > div > img {
  width: 17%;
}

.fadeContainer > div {
  position: fixed;
  width: 100%;
}
.fadeContainer > div:not(.layer0) {
  opacity: 0;
}

.layerBuildingContainer {
  position: fixed;
  height: 100vh;
  /* top: calc((20000px + 100vh) / 7); */
  top: 800px;
  display: flex;
  justify-content: flex-end;
  width: 100vw;
}

.layerBuildingContainer > div {
  width: 100%;
  display: flex;
}
.layerBuildingContainer img {
  bottom: 0;
  position: absolute;
  width: 100%;
}
.layerBuildingContainer .building3Layer img {
  bottom: -1px;
}

.bottomContent {
  display: flex;
  width: 100vw;
  background-color: #0e0e2e;
  height: 300px;
  height: auto;
  z-index: 1;
  flex-direction: column;
}

.bubbleImage {
  margin: 50px;
}

.kingslmanLogo {
  margin: 33px;
}

.introducePeople {
  background-image: url('../assets/images/smallStar.png');
  background-repeat: no-repeat;
  background-size: contain;
  width: 50vw;
}

.introduceFont {
  font-family: MaruBuriot;
  font-size: 30px;
  font-weight: 200;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.81;
  letter-spacing: -0.85px;
  text-align: center;
  color: #e4ba9b;
}

.peopleContainer {
  display: flex;
  justify-content: center;
  align-items: flex-end;
}

.peopleContainer > a > img.photo {
  position: absolute;
  animation: photoFadeOut 0.3s linear forwards;
}
.peopleContainer > a > img.line {
  animation: photoFadeIn 0.3s linear forwards;
}

.peopleContainer > a.active > img.photo {
  animation: photoFadein 0.3s linear forwards;
}
.peopleContainer > a.active > img.line {
  animation: photoFadeOut 0.3s linear forwards;
}

@keyframes photoFadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}
@keyframes photoFadeOut {
  from {
    opacity: 1;
  }

  to {
    opacity: 0;
  }
}
